import Swiper, {
	Navigation,
	Pagination,
	EffectFade,
	Scrollbar,
} from 'swiper';
import { exist } from '../utils';
import 'swiper/css';
import 'swiper/css/effect-fade';

const mbsSlider = (
	sectionSelector = '.js-mbs-section',
	sliderSelector = '.js-mbs-slider',
	scrollbarSelector = '.js-mbs-scrollbar',
	prevArrowSelector = '.js-mbs-arrow-prev',
	nextArrowSelector = '.js-mbs-arrow-next',
	paginationSelector = '.js-mbs-pagination',
	prevAdditionalArrowSelector = '.js-mbs-additional-arrow-prev',
	nextAdditionalArrowSelector = '.js-mbs-additional-arrow-next',
) => {
	const $sections = document.querySelectorAll(sectionSelector);
	if (!exist($sections)) return;

	Swiper.use([Navigation, Pagination, EffectFade, Scrollbar]);

	$sections.forEach($section => {
		const $slider = $section.querySelector(sliderSelector);
		const $scrollbar = $section.querySelector(scrollbarSelector);
		const $prevArrow = $section.querySelector(prevArrowSelector);
		const $nextArrow = $section.querySelector(nextArrowSelector);
		const $pagination = $section.querySelector(paginationSelector);
		if (!exist([$slider, $scrollbar, $prevArrow, $nextArrow, $pagination])) return;

		const paginationLabels = $pagination.dataset.labels ? $pagination.dataset.labels.split(',') : null;

		const mbsSwiper = new Swiper($slider, {
			speed: 800,
			slidesPerView: 1,
			autoHeight: true,
			effect: 'fade',
			fadeEffect: {
				crossFade: true,
			},
			navigation: {
				nextEl: $nextArrow,
				prevEl: $prevArrow,
			},
			pagination: {
				el: $pagination,
				clickable: true,
				renderBullet: (index, className) => {
					const increasedIndexValue = paginationLabels === null ? index + 1 : paginationLabels[index + 1];

					if (index === 0) {
						const value = paginationLabels === null ? index : paginationLabels[index];

						return `
							<span class="${className}">${value}</span>
							<span class="${className}">${increasedIndexValue}</span>
						`;
					} else {
						return `<span class="${className}">${increasedIndexValue}</span>`;
					}
				},
			},
			scrollbar: {
				el: $scrollbar,
				draggable: true,
				dragSize: 213,
			},
			breakpoints: {
				320: {
					scrollbar: {
						dragSize: 91,
					},
				},
				1024: {
					scrollbar: {
						dragSize: 213,
					},
				},
			},
		});

		$slider.addEventListener('click', (e) => {
			if (e.target.closest(prevAdditionalArrowSelector)) mbsSwiper.slidePrev();
			if (e.target.closest(nextAdditionalArrowSelector)) mbsSwiper.slideNext();
		});
	});
};

export default mbsSlider;
